<template>
	<view class="content">
	    <view class="login">
	        <image class="logo" :src="wxConfig.accountInfo.icon" />
	        <view class="title">{{wxConfig.accountInfo.nickname}}</view>
	        <view class="text">首次使用需要授权才能使用哦</view>
	        <button class="userlogin" @click="getUserInfoTap">授权登录</button>
	        <button class="userlogin navigateBack" @click="navigateBack()">暂不登陆</button>
	    </view>
	</view>
</template>

<script>
	const app = getApp();
	export default {
		data() {
			return {
				wxConfig: __wxConfig,
				wxCode: ''
			}
		},
		onLoad() {
			this.getWechatLogin();
		},
		methods: {
			getWechatLogin() {
				let self = this;
				uni.login({
					provider: 'weixin',
					success: function(wxs) {
						self.wxCode = wxs.code;
					}
				})
			},
			getUserInfoTap() {
				let self = this;
				uni.showLoading({title: '正在登录中'});
				uni.getUserProfile({
					desc: '注册身份信息验证',
					success(ues) {
						let user = ues;
						let postData = {
							iv: user.iv,
							code: self.wxCode,
							raw_data: user.rawData,
							signature: user.signature,
							encrypted_data: user.encryptedData
						}
						self.Post(self.Url.wechatDetail, postData).then(wes => {
							if(wes.code === 0){
								uni.hideLoading();
								app.globalData.user = true;
								uni.showToast({title: '授权登陆成功'});
								uni.setStorage({key: '__ddminiUserInfo',data: wes.data});
								setTimeout(function () {
									uni.navigateBack();
								}, 500);
							}else{
								self.getWechatLogin();
								uni.hideLoading();
								uni.showModal({
								  title: '授权失败',
								  content: wes.msg,
								  showCancel: false
								});
							}
						});
					},
					fail() {
						self.getWechatLogin();
						uni.showModal({
						  title: '授权失败',
						  content: '获取授权信息失败，请重新授权登录',
						  showCancel: false
						});
					}
				});
			},
			navigateBack() {
				uni.navigateBack();
			}
		}
	}
</script>

<style lang="less">
	page{
		background-color: #fff;
	}
	.login{
	    height:60vh;
	    width:100%;
	    display:flex;
	    align-items:center;
	    justify-content:center;
	    flex-direction:column;
	    .logo{
			width: 120rpx;
			height: 120rpx;
			border-radius: 100%;
			border: 1px solid #efefef;
	    }
	    .title{
	        color:#46496e;
	        font-size:38rpx;
	        padding-top:20rpx;
			font-weight: normal;
	    }
	    .text{
	        color:#46496e90;
	        padding-top:20rpx;
	        font-size:28rpx;
	        padding-bottom:60rpx;
			font-weight: normal;
	    }
	    .userlogin{
			background-color: #46496e;
			width: 300rpx;
			height: 80rpx;
			font-size: 30rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0;
			color: #fff;
			border-radius: 100rpx;
			margin-bottom: 20rpx;
			font-weight: normal;
	    }
	    .navigateBack{
	        background-color:#46496e20;
	        color:#46496e60;
	        margin-top:10rpx;
	    }
	    button::after{
	        border: 0;
	    }  
	}
</style>
